.FormationTop {
  width: 100%;
  height: 100%;
  position: relative;
}

.FormationTop .box {
  position: absolute;
  width: 100%;
  height: 600px;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

.FormationTop .headBox {
  padding-top: 30px;
  width: 100%;
  height: 600px;
  text-align: center;
  box-sizing: border-box;
  background-image: url("https://images.pexels.com/photos/2135/food-france-morning-breakfast.jpg?auto=compress&cs=tinysrgb&dpr=2&w=500");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-sizing: border-box;
}

.FormationTop .headBox h1 {
  padding-top: 120px;
  font-weight: 500;
  font-size: 32px;
  box-sizing: border-box;
  color: white;
}

.FormationTop .headBox p {
  margin: auto;
  width: 800px;
  text-align: center;
  font-weight: 300;
  box-sizing: border-box;
  font-size: 16px;
  color: white;
  margin-top: 10px;
}

.FormationTop .headBox hr {
  margin-top: 14px;
  height: 0.2px;
  background-color: #eee;
}

.FormationTop .headBox h2 {
  padding-top: 14px;
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  box-sizing: border-box;
  color: white;
}

.FormationTop .textBox {
  margin: auto;
  width: 800px;
  height: 750px;
  box-sizing: border-box;
  color: white;
  padding-top: 10px;
  box-sizing: border-box;
}

.FormationTop .textBox p {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  text-align: justify;
}
