.ExperienceTop {
  display: flex;
  justify-content: center;
  width: 900px;
  height: 600px;
  position: relative;
  margin: 10px auto 0px auto;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.ExperienceTop .container {
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  color: white;
}

.ExperienceTop .container .backimg {
  width: 100%;
  height: 600px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: all 1s;
}

.ExperienceTop .container .textbox {
  position: absolute;
  top: 47.5%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 700px;
  height: 390px;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 50px 50px 0px 50px;
  box-sizing: border-box;
  color: white;
  border-radius: 10px;
}

.ExperienceTop .container .textbox .title {
  font-size: 32px;
  font-weight: 500;
  color: white;
}

.ExperienceTop .container .textbox .subtitle {
  font-size: 16px;
  font-weight: 200;
  margin-bottom: 10px;
  color: white;
}

.ExperienceTop .container .textbox .data {
  margin-top: 20px;
  font-size: 14px;
  font-weight: 400;
  color: white;
}

.ExperienceTop .container .textbox .mission {
  margin-top: 10px;
  font-weight: 400;
  text-align: center;
  position: relative;
  color: white;
  font-size: 16px;
}

.ExperienceTop .container .textbox .mission:before, .ExperienceTop .container .textbox .mission:after {
  content: "";
  background-color: white;
  height: 1px;
  width: 40%;
  position: absolute;
  top: 12px;
}

.ExperienceTop .container .textbox .mission:before {
  left: 0;
}

.ExperienceTop .container .textbox .mission:after {
  right: 0;
}

.ExperienceTop .container .textbox .dect {
  margin-top: 10px;
  font-weight: 300;
  color: white;
}

.ExperienceTop .buttonbox {
  position: absolute;
  width: inherit;
  height: 60px;
  background-color: white;
  bottom: 0;
  box-sizing: border-box;
  border-top: none;
}

.ExperienceTop .buttonbox .controle {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.ExperienceTop .buttonbox .controle .left {
  width: 150px;
  height: 40px;
  cursor: pointer;
  margin-right: 10px;
  margin-top: 10px;
}

.ExperienceTop .buttonbox .controle .right {
  width: 150px;
  height: 40px;
  cursor: pointer;
  margin-left: 10px;
  margin-top: 10px;
}
