.Information {
  width: 900px;
  height: 600px;
  margin: 10px auto 0px auto;
}

.Information .gallery {
  list-style: none;
  padding: 0;
  margin: 10px 0px;
  display: flex;
  height: 600px;
}

.Information .gallery li {
  flex: 1;
  transition: 0.5s;
  position: relative;
  overflow: hidden;
}

.Information .gallery li:last-child {
  border-right: none;
}

.Information .gallery li:nth-child(1) {
  background: url("https://images.pexels.com/photos/7988956/pexels-photo-7988956.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940") no-repeat center center;
}

.Information .gallery li:nth-child(2) {
  background: url("https://www.tourisme-bresil.com/wp-content/uploads/adobestock-215533187-1280x800.jpeg") no-repeat center center;
}

.Information .gallery li:nth-child(3) {
  background: url("https://images.pexels.com/photos/7045526/pexels-photo-7045526.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940") no-repeat center center;
}

.Information .gallery:hover li {
  flex: 1;
}

.Information .gallery li:hover {
  flex: 3;
  filter: grayscale(1);
}

.Information .content_info {
  position: absolute;
  background-color: #fff;
  text-align: center;
  height: 300px;
  bottom: -215px;
  transition: 0.5s;
  padding: 20px;
  box-sizing: border-box;
  padding-top: 50px;
  width: 100%;
}

.Information .content_info h2 {
  font-size: 22px;
  font-weight: bold;
  z-index: 5;
}

.Information .content_info p {
  z-index: 5;
  padding-top: 60px;
  font-weight: 300;
  width: 400px;
  text-align: justify;
  margin: auto;
}

.Information .gallery li:hover .content_info {
  bottom: 0;
  transition-delay: 0.5s;
}

.Information .content_info:before {
  content: "";
  position: absolute;
  background-color: #fff;
  width: 100%;
  height: 50px;
  left: 0;
  top: -2px;
  transform-origin: left bottom;
  transform: rotate(-3deg) scale(1.3);
}
