.contact-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1030px;
  height: 600px;
}

.contact-container .imgs img {
  width: 500px;
  height: 650px;
  margin-right: 30px;
}

.contact-container .contact-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.contact-container .contact-wrapper h1 {
  margin-bottom: 50px;
  font-size: 32px;
  font-weight: bold;
}

.contact-container .contact-wrapper .name-container input {
  width: 500px;
  padding: 10px 20px;
  border: none;
  font-size: 1.163rem;
  font-weight: bold;
  border-radius: 8px;
}

.contact-container .contact-wrapper .email-container {
  margin: 20px 0px;
}

.contact-container .contact-wrapper .email-container input {
  width: 500px;
  padding: 10px 20px;
  border: none;
  font-size: 1.163rem;
  font-weight: bold;
  border-radius: 8px;
}

.contact-container .contact-wrapper .text-container textarea {
  width: 500px;
  font-size: 1.163rem;
  padding: 20px 20px;
  border: none;
  font-weight: bold;
  border-radius: 8px;
}

.contact-container .contact-wrapper .btn-container {
  margin-top: 20px;
}

.contact-container .contact-wrapper .btn-container .btn {
  width: 200px;
  height: 50px;
  padding: 10px 20px;
  background-color: rgba(95, 158, 160, 0.9);
  color: white;
  outline: none;
  font-size: 1.163rem;
  border: none;
  border-radius: 8px;
}
