.snsContainer {
  width: 100%;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: right;
}

.snsContainer span {
  font-size: 0.85rem;
}

.snsContainer .sns {
  display: flex;
  align-items: center;
  justify-content: center;
}

.snsContainer .sns button:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 0;
  bottom: 0;
  left: 0;
  transition: 0.5s;
}

.snsContainer .sns button:hover:before {
  height: 100%;
}

.snsContainer .sns button:nth-child(1):before {
  background-color: #0a66c2;
}

.snsContainer .sns button .fab {
  position: relative;
  transition: 0.5s;
}

.snsContainer .sns button:hover .fab {
  transform: rotateY(360deg);
  color: #fff;
  border-radius: 50%;
}

.snsContainer .sns button {
  border: 1px solid #333333;
  display: inline-block;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  color: #333333;
  text-align: center;
  line-height: 25px;
  font-size: 0.85rem;
  margin-left: 10px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  background-color: white;
}
