.FormationBottom {
  width: 100%;
  height: 100%;
  position: relative;
}

.FormationBottom .box2 {
  position: absolute;
  width: 100%;
  height: 600px;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7);
  margin: auto;
}

.FormationBottom .headBox2 {
  padding-top: 30px;
  width: 100%;
  height: 600px;
  text-align: center;
  box-sizing: border-box;
  background-image: url("https://images.pexels.com/photos/785418/pexels-photo-785418.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-sizing: border-box;
}

.FormationBottom .headBox2 h1 {
  padding-top: 120px;
  font-weight: 500;
  font-size: 32px;
  box-sizing: border-box;
  color: white;
}

.FormationBottom .headBox2 p {
  margin: auto;
  width: 800px;
  text-align: center;
  font-weight: 300;
  box-sizing: border-box;
  font-size: 16px;
  color: white;
  margin-top: 10px;
}

.FormationBottom .headBox2 hr {
  margin-top: 20px;
  height: 0.2px;
  background-color: #eee;
}

.FormationBottom .headBox2 h2 {
  padding-top: 14px;
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  box-sizing: border-box;
  color: white;
}

.FormationBottom .textBox2 {
  margin: auto;
  width: 800px;
  height: 750px;
  box-sizing: border-box;
  color: white;
  padding-top: 10px;
  box-sizing: border-box;
}

.FormationBottom .textBox2 p {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  text-align: justify;
}
