.ProfilTop {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.ProfilTop .container {
  display: flex;
  width: 900px;
  height: 600px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.ProfilTop .container .imgs {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all 1.5s;
  width: 50%;
  height: inherit;
  box-sizing: border-box;
  opacity: 0.9;
}

.ProfilTop .container .text {
  width: 50%;
  height: inherit;
  padding: 50px 20px;
  box-sizing: border-box;
  background-color: white;
  text-align: justify;
  letter-spacing: 1.06px;
}

.ProfilTop .container .text .profile {
  width: inherit;
  height: inherit;
  display: inline-flex;
}

.ProfilTop .container .text .name {
  height: 60px;
  font-size: 40px;
  line-height: 1.5;
  font-style: italic;
  font-weight: bold;
}

.ProfilTop .container .text .name_job {
  display: inline-block;
  vertical-align: top;
  height: 60px;
  overflow: hidden;
  padding-right: 20px;
  margin-left: 20px;
}

.ProfilTop .container .text .name_job_list {
  animation-name: rolling;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-direction: reverse;
}

.ProfilTop .container .text .name_job_list li {
  font-weight: bold;
}

.ProfilTop .container .text p {
  width: 410px;
  height: 300px;
  font-weight: 300;
  margin-top: 20px;
  font-size: 1.038rem;
}

@keyframes rolling {
  10% {
    margin-top: 0em;
  }
  100% {
    margin-top: -3em;
  }
}
