.faq-container {
  margin-top: 50px;
  padding-right: 2rem;
  width: 45rem;
}

h2 {
  font-size: 1.163rem;
  font-weight: bold;
}

.accordion {
  margin-top: 20px;
}

.accordion .accordion-item {
  border-bottom: 1px solid #e5e5e5;
}

.accordion .accordion-item button[aria-expanded="true"] {
  border-bottom: 1px solid #ffa500;
}

.accordion button {
  position: relative;
  display: block;
  text-align: left;
  width: 100%;
  padding: 1em 0;
  color: #000;
  font-size: 0.913rem;
  font-weight: 400;
  border: none;
  background: none;
  outline: none;
}

.accordion button:hover, .accordion button:focus {
  cursor: pointer;
  color: #ffa500;
}

.accordion button:hover::after, .accordion button:focus::after {
  cursor: pointer;
  color: #ffa500;
  border: 1px solid #ffa500;
}

.accordion button .accordion-title {
  padding: 1em 1.5em 1em 0;
  font-size: 1.038rem;
}

.accordion button .faq-icon {
  display: inline-block;
  position: absolute;
  top: 18px;
  right: 0;
  width: 22px;
  height: 22px;
  border: 1px solid;
  border-radius: 22px;
}

.accordion button .faq-icon::before {
  display: block;
  position: absolute;
  content: "";
  top: 9px;
  left: 5px;
  width: 10px;
  height: 2px;
  background: currentColor;
}

.accordion button .faq-icon::after {
  display: block;
  position: absolute;
  content: "";
  top: 5px;
  left: 9px;
  width: 2px;
  height: 10px;
  background: currentColor;
}

.accordion button[aria-expanded="true"] {
  color: #ffa500;
}

.accordion button[aria-expanded="true"] .faq-icon::after {
  width: 0;
}

.accordion button[aria-expanded="true"] + .accordion-content {
  opacity: 1;
  max-height: 100px;
  transition: all 300ms linear;
  will-change: opacity, max-height;
}

.accordion button[aria-expanded="true"] .accordion-title {
  font-weight: bold;
}

.accordion .accordion-content {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 250ms linear, max-height 250ms linear;
  will-change: opacity, max-height;
  display: flex;
}

.accordion .accordion-content p {
  font-size: 0.913rem;
  font-weight: 400;
  line-height: 100px;
}

.accordion .accordion-content a {
  display: inline-block;
  width: 80px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  color: royalblue;
  font-size: 0.913rem;
  font-weight: 400;
  font-weight: bold;
}
